import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';

const aboutUsList = [
	{
		first: 'Mari seara',
		second:
			'  pentru copii cu vrste ntre 7 i 10 ani, cu teme prestabilite care dezvolt atenia, logica i cooperarea.'
	},
	{
		first: 'Vineri seara',
		second: '  pentru copii ntre 11 i 15 ani, cu jocuri de mister, strategie i lucru n echip.'
	},
	{
		first: 'Smbt',
		second:
			'  eveniment deschis tuturor copiilor ntre 7 i 15 ani, n care punem la dispoziie ntreaga noastr colecie de jocuri de societate. Copiii au libertatea s exploreze, s testeze i s descopere, ghidai cu grij de echipa noastr.'
	}
];

export default function PageDescription() {
	return (
		<Box maxWidth="md" mx="auto" textAlign="center" marginTop={2}>
			<Typography variant="h4">Despre evenimentele noastre</Typography>
			<Box maxWidth="md" mx="auto" textAlign="left" marginTop={2} gap={2} display="flex" flexDirection={'column'}>
				<Paper sx={{padding: 2}}>
					<Typography>
						ncepnd cu 24 iunie, n fiecare sptmn organizm trei evenimente interactive, menite s aduc mpreun
						joaca, nvarea i socializarea  ntr-un mediu sigur, cald i prietenos, ghidate de echipa noastr de
						facilitatori pasionai:
					</Typography>
					{aboutUsList.map(({first, second}) => (
						<Box key={first} flexDirection={'row'} textAlign={'start'} padding={1}>
							<Typography display="inline" fontWeight={700}>
								
							</Typography>{' '}
							<Typography display="inline" fontWeight={700}>
								{first}
							</Typography>{' '}
							<Typography display="inline">{second}</Typography>
						</Box>
					))}
				</Paper>
				<Paper sx={{padding: 2}}>
					<Typography>
						 n tot acest timp, prinii se pot relaxa ntr-un spaiu separat, cu ceai, cafea i acces la colecia
						noastr de jocuri  pentru c bucuria jocului nu are vrst!
					</Typography>
				</Paper>
				<Paper sx={{padding: 2}}>
					<Typography> Costul participrii la oricare din evenimentele noastre este de 50 de lei.</Typography>
					<Typography> Prima participare este 25 de lei.</Typography>
				</Paper>
				<Paper sx={{padding: 2}}>
					<Typography fontWeight={700}> V dorii s participai, dar programul nu se potrivete?</Typography>
					<Typography>
						Scriei-ne! Putem organiza evenimente la cerere, n funcie de disponibilitatea voastr i a noastr.
					</Typography>
					<Typography display="inline" fontWeight={700}>
						
					</Typography>{' '}
					<Typography display="inline" fontWeight={700}>
						Singura condiie:{' '}
					</Typography>
					<Typography display="inline">cel puin 3 copii participani.</Typography>
					<Typography>* Pentru evenimentele la cerere nu se aplic reducerea la prima participare</Typography>
				</Paper>
				<Paper sx={{padding: 2}}>
					<Typography fontWeight={700}>
						Pentru nscriere la oricare dintre evenimentele noastre, nu ezitai s ne contactai!
					</Typography>
				</Paper>
			</Box>
		</Box>
	);
}
