import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';

import BolderCards from './components/BoldedCard';
import DotCard from './components/DotCard';

const aboutUsList = [
	{
		first: 'O varietate de jocuri atent alese',
		second: 'menite s stimuleze diferite abiliti cognitive i sociale'
	},
	{
		first: 'Un mediu distractiv i relaxat',
		second:
			'n care copiii nva s gndeasc strategic, s i elaboreze i s urmeze un plan, s analizeze situaii i s identifice soluii eficiente'
	},
	{
		first: 'Lucru n grupuri mici',
		second:
			'organizate n funcie de vrst, pentru a susine dezvoltarea social: copiii nva s relaioneze, s colaboreze, s i gestioneze emoiile i s i dezvolte reziliena'
	},
	{
		first: 'Sprijin constant din partea echipei',
		second:
			'astfel nct fiecare copil s poat profita la maximum de experien, ntr-un mod adaptat ritmului i nevoilor sale'
	}
];
const pentruCine = [
	'Copiii care iubesc jocurile de societate sau vor s le descopere;',
	'Cei care au nevoie de sprijin n dezvoltarea ateniei, concentrrii sau gndirii logice;',
	'Copiii care vor s i mbunteasc interaciunea social i gestionarea emoiilor;'
];
const benefits = [
	'Dezvoltarea gndirii critice i a capacitii de rezolvare a problemelor;',
	'mbuntirea comunicrii i a lucrului n echip;',
	'Creterea ncrederii n sine i a motivaiei personale;',
	'Formarea unor strategii sntoase de gestionare a emoiilor i a eecului;',
	'Cultivarea rbdrii, perseverenei i a unei atitudini constructive fa de provocri.'
];

const desfasurare = [
	{
		first: 'Evenimentele se desfoar sptmnal',
		second: 'cu o durat de aproximativ 2 ore fiecare'
	},
	{
		first: 'Copiii sunt mprii n grupuri mici',
		second: 'n funcie de vrst i nivel de experien'
	},
	{
		first: 'Fiecare sesiune include un mix echilibrat de jocuri',
		second: 'n funcie de tema evenimentului'
	},
	{
		first: 'Activitile sunt ghidate de echipa noastr',
		second: 'cu experien n lucru cu copiii i pasiune pentru jocuri de societate'
	}
];

export default function AboutUs() {
	return (
		<Box maxWidth="md" mx="auto" textAlign="center" marginTop={2}>
			<Typography variant="h4">Despre noi</Typography>
			<Box maxWidth="md" mx="auto" textAlign="left" marginTop={2}>
				<Paper sx={{padding: 2, flexGrow: 1}}>
					<Typography>
						Suntem o echip pasionat de jocuri de societate, care a descoperit, de-a lungul timpului, ct de valoroase
						pot fi acestea pentru dezvoltarea personal. Prin intermediul jocului, ne-am antrenat gndirea strategic,
						ne-am format o nelegere mai clar asupra situaiilor din jur i am nvat s lum decizii adaptate
						contextului.
					</Typography>
					<Typography>
						Pentru noi, jocul nu este doar o form de relaxare, ci i un instrument prin care ne meninem mintea activ
						i ne dezvoltm n mod constant. Din aceast convingere, ne-am propus s aducem beneficiile jocurilor de
						societate i n universul copiilor. Credem c, ntr-un cadru distractiv i sigur, acetia i pot dezvolta
						abiliti eseniale precum atenia, gndirea logic, comunicarea i cooperarea.
					</Typography>
				</Paper>
			</Box>
			<Grid container maxWidth="md" mx="auto" spacing={2} my={2}>
				<BolderCards title="Ce oferim?" list={aboutUsList} />
				<DotCard
					title="Pentru cine este potrivit?"
					description="Acest program se adreseaz copiilor cu vrste ntre 7 i 15 ani, care sunt curioi, activi i dornici s
							descopere jocuri noi ntr-un cadru organizat. Este ideal pentru:"
					list={pentruCine}
				/>
				<DotCard
					title="Beneficiile pe termen lung"
					description="Participarea constant la aceste activiti contribuie la:"
					list={benefits}
				/>
				<BolderCards title="Cum funcioneaz?" list={desfasurare} />
			</Grid>
		</Box>
	);
}
