import Container from '@mui/material/Container';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';

import GamesTable from './components/games-table/GamesTable';

export default function GameListPage() {
	return (
		<Container
			sx={{
				display: 'flex',
				flexDirection: 'column',
				alignItems: 'center',
				pt: {xs: 14, sm: 12},
				pb: {xs: 8, sm: 12},
				px: 2
			}}
		>
			<Stack spacing={2} useFlexGap sx={{alignItems: 'center', width: {xs: '100%', sm: '70%'}}}>
				<Typography
					variant="h1"
					sx={{
						fontSize: 'clamp(3rem, 10vw, 3.5rem)',
						textAlign: 'center'
					}}
				>
					Jocuri disponibile
				</Typography>
				<Typography
					variant="h6"
					sx={{
						textAlign: 'center',
						color: 'text.primary',
						width: {sm: '100%', md: '80%'}
					}}
				>
					Mai jos putei vedea toate jocurile disponibile la noi. Cele cu complexitate uor sunt disponibile n
					permanen. Dac dorii jocurile mai complexe, v rog s ne anunai i vor fi aduse n locaie.
				</Typography>
			</Stack>
			<GamesTable />
		</Container>
	);
}
